<template>
  <BaseLayout>
    <Breadcrumbs />
    <section class="static pt-7">
      <BContainer>
        <BRow>
          <BCol lg="3" class="d-none d-lg-block">
            <div class="sidebar">
              <span class="sidebar-heading">Links</span>
              <cmsBlockSimple identifier="side-menu-cms" />
            </div>
          </BCol>
          <b-col lg="9">
            <h1>{{ cmsPageContent?.title }}</h1>
            <!-- <v-runtime-template
              v-if="cmsPageContent?.content"
              :template="`<div class='cms-content'>${cmsPageContent?.content}</div>`"
            ></v-runtime-template> -->
            <div
              class="cms-content"
              v-if="cmsPageContent?.content"
              v-html="cmsPageContent?.content"
            ></div>
          </b-col>
        </BRow>
      </BContainer>
    </section>
  </BaseLayout>
</template>

<script setup>
import { computed, onMounted } from "vue";
import { useStore } from "vuex";
import { useHead } from "@unhead/vue";
import BaseLayout from "@/base/core/components/common/BaseLayout";
import cmsBlockSimple from "@/base/core/components/core/BlockSimple";
import Breadcrumbs from "@/base/core/components/Breadcrumbs";
// import VRuntimeTemplate from "vue3-runtime-template";

const store = useStore();

// Computed property
const cmsPageContent = computed(() => store.state.cmsPage.current);

// Set up meta information using useHead
useHead({
  title: () => cmsPageContent.value?.title,
  meta: [
    { name: "title", content: () => cmsPageContent.value?.meta_title },
    { name: "keywords", content: () => cmsPageContent.value?.meta_keywords },
    {
      name: "description",
      content: () => cmsPageContent.value?.meta_description,
    },
  ],
});

onMounted(() => {
  const bcrumb = { current: cmsPageContent.value?.title, routes: [] };
  store.commit("breadcrumbs/set", bcrumb);
});
</script>
